@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.content {
    scroll-behavior: smooth;
    display: flex;
    flex-direction: column;
    max-width: 1100px;
    text-align: start;
}

.content__row{
    display: flex;
    justify-content: space-between;
}

ol { 
    padding-left: 50px; 
}

li { 
  padding-left: 16px;
  margin-top: 24px;
  position: relative;
  font-size: 16px;
  line-height: 20px;
}
  
li:before {
    content: '';
    display: block;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 2px dashed #0276FF;
    position: absolute; 
    top: -5px;
    left: -29px;
  }

strong {
    font-family: 'Space Mono', monospace; 
  }

  .highlight{
    background-color: #0291ff2c;
  }





