.example{
    display: flex;
    max-width: 300px;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    border: #006AE8 dashed 2px; 
    padding: 30px;
    margin-right: 100px;
    margin-top: 150px;
}

.ex_event1{
    margin-right: 200px;
    margin-top: 80px;
}

.ex_event2{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 150px;
  margin-top: 100px;
}

.btns{
    display: flex;
    flex-direction: column;
    min-height: 80px;
    max-width: 155px;
    text-align: center;
    justify-content: space-between;
}

.result{
    margin-right: 30px;
    background-color: #0276FF;
    border-radius: 50%;
    text-align: center;
    width: 50px;
    height: 50px;
    color: #fff;
    font-size: 200%;
}

.btn {
  align-items: center;
  appearance: button;
  background-color: #0276FF;
  border-radius: 8px;
  border-style: none;
  box-shadow: rgba(255, 255, 255, 0.26) 0 1px 2px inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  font-family: "RM Neue",sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  padding: 10px 21px;
  text-align: center;
  text-transform: none;
  transition: color .13s ease-in-out,background .13s ease-in-out,opacity .13s ease-in-out,box-shadow .13s ease-in-out;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.btn:active {
  background-color: #006AE8;
}

.btn:hover {
  background-color: #1C84FF;
}

input{
  border-bottom: 2px solid #1C84FF;
  border-top: none;
  border-left: none;
  border-right: none;
}

